<template>
  <div>
    <div style="font-size: 16px; position: absolute; top: 12px; right: 40px">
      <el-link
        :underline="false"
        type="primary"
        style="font-size: 16px; cursor: pointer; margin-top: 10px"
        @click="exportExcel"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >导出</el-link
      >
    </div>

    <div id="top">
      <div>
        <span>微信号</span>
        <el-input
          v-model="params.openid"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>ID</span>
        <el-input
          v-model="params.id"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>手机号码</span>
        <el-input
          v-model="params.phoneNumber"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span style="margin-left: 20px">状态</span>
        <el-select
          v-model="params.status"
          placeholder="请选择"
          class="search"
          size="mini"
        >
          <el-option label="禁言" value="disabled"></el-option>
          <el-option label="正常" value="normal"></el-option>
        </el-select>

        <el-button-group style="margin-left: 20px">
          <el-button type="primary" size="mini" @click="search">搜索</el-button>
          <el-button size="mini" @click="resetTable">重置</el-button>
        </el-button-group>
      </div>
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 130"
      style="width: 100%; text-align: center; line-height: 18px"
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="微信号"
        align="center"
        prop="openid"
        width="250"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="手机号码" align="center" prop="phoneNumber">
      </el-table-column>
      <el-table-column
        label="微信名"
        align="center"
        width="100"
        prop="username"
      >
      </el-table-column>

      <el-table-column label="状态" align="center" prop="status">
        <template slot-scope="{ row }">
          {{ row.status == "disabled" ? "禁言中" : "正常" }}
        </template>
      </el-table-column>

      <el-table-column
        label="id"
        align="center"
        prop="id"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="vip"
        align="center"
        show-overflow-tooltip
      >
      <template slot-scope="{ row }">
          {{ row.vip ? "是" : "否" }}
        </template>
    </el-table-column>

      <el-table-column
        label="积分"
        width="100"
        align="center"
        prop="points"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="打卡记录"
        width="100"
        align="center"
        prop="remarks"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="{ row }">
          <el-link type="success" @click="edit(row)"   style="margin-right: 10px">编辑</el-link>
          <el-link
            type="warning"
            @click="handleDisabled(row, 'disabled')"
            style="margin-right: 10px"
            >禁言</el-link
          >
          <el-link type="success" @click="handleDisabled(row, 'normal')"
            >解封</el-link
          >
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 150, 200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="float: right; margin-top: 20px"
    >
    </el-pagination>

    <el-dialog title="编辑" :visible.sync="dialogFormVisible" @close="reset">
      <el-form :model="form" size="small">
        <el-form-item label="VIP日期范围">
          <el-date-picker
            v-model="form.date"
            type="daterange"
            align="right"
            unlink-panels
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
        <el-button type="primary" @click="submit" size="small"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "User_management",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      currentPage: 1, //当前页码
      pageSize: 20,
      total: 0,
      params: {
        openid: "",
        id: "",
        phoneNumber: "",
        status: "",
      },
      tableData: [],
      dialogFormVisible: false,
      form: {
        date: [],
      },
      row:{}
    };
  },
  created() {},
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$reqPost("/user/allData", {
        ...this.params,
      }).then((res) => {
        if (res.code == 200) {
          this.tableData = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    handleDisabled(row, status) {
      let openid = row.openid;
      this.$reqPost("/user/disabled", {
        openid: openid,
        status: status,
      }).then((res) => {
        if (res.code == 200) {
          this.getData();
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    handleSelectionChange(val) {
      console.log(val);
    },
    search() {
      console.log(this.params);
      this.getData();
    },
    resetTable() {
      this.params = {
        openid: "",
        id: "",
        phoneNumber: "",
        status: "",
      };
      this.getData();
    },
    reset(){
      this.row={}
    },
    edit(row) {
      this.row=row;
      if(row.vip){
        let arr=row.vip.split(',')
        arr[0]=new Date(Number(arr[0])).toLocaleDateString()
        arr[1]=new Date(Number(arr[1])).toLocaleDateString()
        console.log(arr)
        this.form={
          date:arr
        }
      }
      this.dialogFormVisible = true;
    },
    submit(){
      let date=this.form.date
      let start=new Date(date[0]).getTime()
      let end=new Date(date[1]).getTime()
     
      this.$reqPost("/user/setVip",{
       date:[start.toString(),end.toString()],
       openid:this.row.openid
      }).then(res=>{
          if(res.code==200){
            this.$message.success('提交成功')
            this.dialogFormVisible = false
            this.getData()
            
          }else{
            this.$message.error(res.msg);
          }
      })
    },
    exportExcel() {
      this.$reqGet("/user/exportExcel").then((res) => {
        if (res.code == 200) {
          console.log(res);
        } else {
          this.$message.error(res.msg);
        }
      });
    },
  },
};
</script>
<style scoped>
#top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 14px;
  color: #595959;
}
.search {
  width: 140px;
  margin-left: 10px;
  margin-right: 10px;
}
</style>
